import { Component } from 'react';
import 'bulma/css/bulma.css';
import cn from 'classnames';
import './App.scss';

export const goods = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

interface State {
  selectedGood: string;
}

export class App extends Component<{}, State> {
  state = {
    selectedGood: 'Jam',
  };

  removeGoodHandler = () => {
    this.setState({ selectedGood: '' });
  };

  addGoodHandler = (item: string) => {
    this.setState({ selectedGood: item });
  };

  render() {
    const { selectedGood } = this.state;

    return (
      <main className="section container">
        {!selectedGood && <h1 className="title">No goods selected</h1>}

        {selectedGood
          && (
            <h1 className="title is-flex is-align-items-center">
              {`${selectedGood} is selected`}
              {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
              <button
                data-cy="ClearButton"
                type="button"
                className="delete ml-3"
                onClick={this.removeGoodHandler}
              />
            </h1>
          )}

        <table className="table">
          <tbody>
            {goods.map((item) => (
              <tr
                key={item}
                data-cy="Good"
                className={cn({
                  'has-background-success-light': item === selectedGood,
                })}
              >
                <td>
                  {!(item === selectedGood)
                    && (
                      <button
                        data-cy="AddButton"
                        type="button"
                        className="button"
                        onClick={() => (this.addGoodHandler(item))}
                      >
                        +
                      </button>
                    )}

                  {item === selectedGood
                  && (
                    <button
                      data-cy="RemoveButton"
                      type="button"
                      className="button is-info"
                      onClick={this.removeGoodHandler}
                    >
                      -
                    </button>
                  )}
                </td>
                <td data-cy="GoodTitle" className="is-vcentered">
                  {item}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </main>
    );
  }
}
